/* 在CSS文件中引入antd的css文件 */
@import '~antd/dist/antd.css';
*{
    padding: 0;
    margin: 0;
}
body{
    background-color: #ccc;
}
.navbar{
    height: 50px;
    background-color: #444;
}
.navbar header{
    width: 90%;
    line-height: 50px;
    margin: 0 auto;
}
.navbar header a{
    margin: 0 20px;
    color: #fff;
}
.navbar img{
    width: 120px;
}

/* main */
.main{
    width: 90%;
    max-width: 1400px;
    min-width: 960px;
    margin: 15px auto;
    min-height: 400px;
}
.panel{
    /* width: 900px; */
    padding: 0;
    margin-right: 305px;
    /* height: 500px; */
    /* background-color: royalblue; */
}
.sidebar{
    width: 290px;
    font-size: 14px;
    float: right;
    margin-bottom: 20px;
    /* height: 500px;
    background-color: red; */
}
.header {
    padding: 10px;
    background-color:#f6f6f6;
    border-radius: 3px 3px 0 0;
}
.active-tab {
    background-color: #80bd01;
    color:#fff;
    padding: 3px 4px;
    border-radius: 3px;
}
.topic-tab {
    margin: 0 10px;

    color: #80bd01;
}
.inner{
    padding: 0;
    border-radius: 0 0 3px 3px;
    background-color: #fff;
}
.inner li {
    line-height: 2em;
}
.topic-list{
    margin: 0;
}
.cell:nth-child(1) {
    border-top: none;
}
.cell {
    position: relative;
    padding: 10px 0 10px 10px;
    padding-right: 10px;
    font-size: 14px;
    overflow: hidden;
    border-top: 1px solid #f0f0f0;
}
.cell:hover {
    background: 
    #f5f5f5;
}
.pull-left {
    float: left;
}
.user_avatar img {
    width: 30px;
    height: 30px;
    border-radius: 3px;
    max-width: 100%;
    vertical-align: middle;
    border: 0;
}
.reply_count {
    width: 70px;
    display: inline-block;
    text-align: center;
}
.pull-left {
    float: left;
}
.count_of_replies {
    color: #9e78c0;
}
.count_seperator {
    /* margin: 0 -3px; */
    font-size: 10px;
}
.count_of_visits {
    font-size: 10px;
    color: 
    #b4b4b4;
}
.last_time {
    text-decoration: none;
    font-size: 11px;
    display: inline-block;
    margin-left: 20px;
    color: #777;
}
.pull-right {
    float: right;
}
.user_small_avatar {
    height: 18px;
    width: 18px;
    vertical-align: middle;
    margin-right: .5em;
    border-radius: 3px;
    border: 0;
}
.last_active_time {
    text-align: right;
    min-width: 50px;
    display: inline-block;
    white-space: nowrap;
}
.topic_title_wrapper {
    white-space: nowrap;
    text-overflow: ellipsis;
}
.topic_title {
    color: #333;
    max-width: 70%;
    white-space: nowrap;
    display: inline-block;
    vertical-align: middle;
    font-size: 16px;
    line-height: 30px;
    overflow: hidden;
}
.topic_title:hover {
    color: #005580;
    text-decoration: underline;
}
.put_top {
    background: #80bd01;
    padding: 2px 4px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    color:#fff;
    font-size: 12px;
}
.pagination {
    height: 40px;
    margin: 10px 0 0 10px;
}
.pagination ul {
    display: inline-block;
    margin-bottom: 0;
    margin-left: 0;
    border-radius: 4px;
    padding: 0;
    margin: 0 0 10px 25px;
    box-shadow: none;
} 
.pagination ul > li {
    display: inline;
}
.pagination ul > li :hover {
    background-color: 
    #f5f5f5;
}
.panel .inner {
    color: #778087;
}
.pagination ul > li {
    float: left;
    padding: 4px 12px;
    line-height: 20px;
    text-decoration: none;
    background-color: #fff;
    border: 1px solid #ddd;
}
.navbar-search {
    margin-top: 6px;
    float: left;
    margin-bottom: 0;
    position: absolute;
    left: 210px;
    top: 0px;
}
.search-query {
    background: url(https://static2.cnodejs.org/public/images/search.e53b380a.hashed.png) 4px 4px no-repeat#888;
    padding: 3px 5px 3px 22px;
    color:#666;
    border: 0;
    margin-top: 2px;
    margin-bottom: 10px;
    font-size: 13px;
    font-weight: 400;
    line-height: 1;
    border-radius: 15px;
    width: 206px;
    margin-left: 0;
    display: inline-block;
    height: 25px;
    vertical-align: middle;
}
.breadcrumb {
    padding: 0;
    margin: 0;
    border: none;
    background: 0 0;
    box-shadow: none;
    border-radius: 4px;
}
.breadcrumb > li {
    text-shadow: none;
    display: inline-block;
    line-height: 20px;
    list-style: none;
}
.breadcrumb a {
    color: #80bd01;
}
.divider {
    padding: 0 5px;
    color: #ccc;
}
.breadcrumb > .active {
    color: #999;
}
.form-horizontal {
    margin: 0 0 20px;
    padding-top: 40px;
}
.control-group {
    margin-bottom: 20px;
}
.control-label {
    float: left;
    width: 160px;
    padding-top: 5px;
    text-align: right;
}
label {
    display: block;
    margin-bottom: 5px;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    cursor: pointer;
}
.controls {
    margin-left: 180px;
}
.form-horizontal input{
    display: inline-block;
    margin-bottom: 0;
    vertical-align: middle;
}
.input-xlarge{
    background: hsla(0,0%,0%,0);
    border: 1px solid #ccc;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
    transition: border linear .2s,box-shadow linear .2s;
    height: 20px;
    padding: 4px 6px;
    font-size: 14px;
    line-height: 20px;
    color:#555;
    border-radius: 4px;
    width: 270px;
    height: 30px;
}
.form-actions {
    padding-left: 180px;
    border-top: none;
    background-color:#fff;
    max-height: 40px;
    padding: 19px 20px 80px;
    margin-top: 20px;
    margin-bottom: 20px;
}
.span-primary {
    border-radius: 3px;
    background-color: #08c;
    display: inline-block;
    margin-bottom: 0;
    vertical-align: middle;
    float: none;
    margin-left: 0;
    margin-right: 10px;
    width: auto;
    cursor: pointer;
    border: none;
    padding: 3px 10px;
    font-size: 14px;
    letter-spacing: 2px;
    box-shadow: none;
    line-height: 2em;
    color:#fff;
}
.span-info {
    display: inline-block;
    float: none;
    padding: 3px 10px;
    border: none;
    margin: 0;
    font-size: 14px;
    transition: all .2s ease-in-out;
    cursor: pointer;
    letter-spacing: 2px;
    box-shadow: none;
    border-radius: 3px;
    line-height: 2em;
    vertical-align: middle;
    color: #fff;
    background-color:#5bc0de;
}
#forgot_password {
    margin-left: 1em;
    color: #778087;
}
.s-panel{
    font-size: 13px;
    margin-bottom: 13px;
    padding: 10px 10px;
    background-color:#fff;
    border-radius: 4px;
}
.ads img{
    height: auto;
    max-width: 100%;
    vertical-align: middle;
    border: 0;
}
.sep10 {
    height: 10px;
}
.cnode-app-download{
    text-align: center;
}
.cnode-app-download img{
    vertical-align: middle;
    border-style: none;
}
